// Body
$body-bg: #f8fafc;

// Typography
$font-family: Century Gothic,CenturyGothic,AppleGothic,sans-serif;
$font-size-base: 0.8rem;
$line-height-base: 1.5;

$black: #000;

// Activar sombras
$enable-shadows: true;
$box-shadow: 0 .5rem 1rem rgba($black, .15);
$box-shadow-sm: 0 .125rem .25rem rgba($black, .075);
$box-shadow-lg: 0 1rem 3rem rgba($black, .175);
$box-shadow-inset: inset 0 1px 2px rgba($black, .075);

//Activar gradientes
$enable-gradients: true;

// Tamaño de los rounded
$border-radius: .25rem;
$border-radius-sm: .2rem;
$border-radius-lg: .3rem;
$border-radius-xl: .6rem;
$border-radius-pill: 50rem;

// Cards
$card-border-radius: $border-radius-lg;
$card-box-shadow: $box-shadow-lg;


$theme-colors: (
  "primary":    #0d6efd,
  "secondary":  #6c757d,
  "success":    #198754,
  "info":       #0dcaf0,
  "warning":    #ffc107,
  "danger":     #dc3545,
  "light":      #f8f9fa,
  "dark":       #212529,

  "myColor":    rgb(28,47,118),
);

$utilities: (
  "width": (
    property: width,
    class: w,
    responsive: true,
    values: (
        10: 10%,
        25: 25%,
        33: 33%,
        45: 45%,
        50: 50%,
        75: 75%,
        90: 90%,
        100: 100%,
        auto: auto
    )
  ),
  "height": (
    property: height,
    class: h,
    responsive: true,
    values: (
      5: 5%,
      25: 25%,
      50: 50%,
      75: 75%,
      95: 95%,
      100: 100%,
      auto: auto
    )
  ),
  "rounded": (
    property: border-radius,
    class: rounded,
    values: (
      null: $border-radius,
      0: 0,
      1: $border-radius-sm,
      2: $border-radius,
      3: $border-radius-lg,
      4: $border-radius-xl,
      circle: 50%,
      pill: $border-radius-pill
    )
  ),
  "shadow": (
      property: box-shadow,
      class: shadow,
      values: (
        null: $box-shadow,
        sm: $box-shadow-sm,
        lg: $box-shadow-lg,
        none: none,
      )
    ),
);